// stylelint-disable declaration-no-important

//
// Border
//

.border         { border: $border-width solid $border-color !important; }
.border-top     { border-top: $border-width solid $border-color !important; }
.border-right   { border-right: $border-width solid $border-color !important; }
.border-bottom  { border-bottom: $border-width solid $border-color !important; }
.border-left    { border-left: $border-width solid $border-color !important; }

.border-0        { border: 0 !important; }
.border-top-0    { border-top: 0 !important; }
.border-right-0  { border-right: 0 !important; }
.border-bottom-0 { border-bottom: 0 !important; }
.border-left-0   { border-left: 0 !important; }

@each $color, $value in $theme-colors {
  .border-#{$color} {
    border-color: $value !important;
  }
}

.border-white {
  border-color: $white !important;
}

//
// Border-radius
//

.rounded {
  border-radius: $border-radius  !important;
}
.rounded-top {
  border-top-left-radius: $border-radius !important;
  border-top-right-radius: $border-radius !important;
}
.rounded-right {
  border-top-right-radius: $border-radius !important;
  border-bottom-right-radius: $border-radius !important;
}
.rounded-bottom {
  border-bottom-right-radius: $border-radius !important;
  border-bottom-left-radius: $border-radius !important;
}
.rounded-left {
  border-top-left-radius: $border-radius !important;
  border-bottom-left-radius: $border-radius !important;
}

.rounded-circle {
  border-radius: 50% !important;
}

.rounded-0 {
  border-radius: 0 !important;
}
.rounded-1 {
  border-radius: $border-radius * 2 !important;
}
.rounded-2 {
  border-radius: $border-radius * 3 !important;
}
.rounded-3 {
  border-radius: $border-radius * 4 !important;
}
.rounded-4 {
    border-radius: $border-radius * 5 !important;
}
.rounded-5 {
  border-radius: $border-radius * 6 !important;
}
.rounded-6 {
  border-radius: $border-radius * 7 !important;
}
.rounded-7 {
  border-radius: $border-radius * 8 !important;
}
.rounded-8 {
  border-radius: $border-radius * 100 !important;
}
.rounded-9 {
  border-radius: 10vh * 10 !important;
}
.rounded-10 {
  border-radius: 10vw * 10 !important;
}
